@font-face {
  font-family: "gaultier";
  src: url("/assets/fonts/gaultier-medium-webfont.woff2") format("woff2"), url("/assets/fonts/gaultier-medium-webfont.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}
:root {
  --color-3: #F71B3D;
}

html {
  background: #222528;
  color: #B3B5C2;
  font-family: "gaultier", "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", "SongTi TC", sans-serif;
  font-variant-numeric: lining-nums;
}

footer {
  color: #B3B5C2;
  font-weight: normal;
}

main {
  font-size: 1em;
}

header {
  margin-top: 1em;
  padding-top: 7em;
  margin-right: 1em;
  background: 25%/contain no-repeat url("/assets/images/second-coming-sun.png");
}

h1 {
  margin-left: 32%;
  margin-bottom: 0.5em;
  font-size: 2em;
  text-align: left;
  color: #D6D9E9;
  letter-spacing: 0.3em;
}

.poem-attribution {
  margin-top: 4em;
  text-align: right;
}